body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h2 {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
    text-align: center;

}
.pokemon-list {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 20px;
}

.pokemon-card {
  background-color: #dfadcc;
  padding: 20px;
  text-align: center;
}

.pokemon-image {
  width: 100%;
  max-width: 200px;
  height: auto;
}

.next-button {
  margin-top: 20px;
  text-align: center;
  background-color: #90b9e4;
}

.next-button button {
  background-color: #90b9e4;
  color: #fff;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  align-items: center;
}

.next-button button:hover {
  background-color: #92bce9;
}
